/* Estilos generales */
body {
  font-family: 'Arial', sans-serif;
  background-color: #f0f8ff;
  color: #333;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Estilo del contenedor principal */
.App {
  max-width: 800px;
  margin: 20px auto;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  background-color: #ffffff;
}

/* Estilo del encabezado */
h1 {
  font-size: 2.5em;
  color: #4a90e2;
}

/* Estilo del contador de animales */
h2 {
  font-size: 1.9em;
  color: #333;
}

/* Estilo de las tarjetas de animales */
.animal-card {
  display: inline-block;
  margin: 10px;
  padding: 10px;
  border: 2px solid #4a90e2;
  border-radius: 8px;
  background-color: #e1f5fe;
  text-align: center;
  transition: transform 0.2s;
}

.animal-card:hover {
  transform: scale(1.05);
}

/* Estilo de las imágenes de los animales */
.animal-card img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
}

/* Estilo del botón */
button {
  padding: 10px 20px;
  margin: 20px 0;
  font-size: 1.2em;
  color: #fff;
  background-color: #4a90e2;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #357ab8;
}

/* Estilo de la lista de animales */
.animal-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
